      aspects, such as greater functionality of the printed tissues through better vascularization and\n                    exploring the synergy with other additive manufacturing techniques. Our team's multidisciplinarity\n                    enables the control of the process from the early stage of machine design to cell preparation, which\n                    allows us to adapt the 3D printers to the specific needs of different projects and research\n                    lines.<br>The main applications of our bioprinted constructs in the lab are the following:</p> <ul class=\"font-avenir font-sm text-base list-inside list-disc align-center tracking-mid leading-6 py-3\"><li>Study of tissue and organ regeneration</li> <li>Explore mechanisms of diseases</li> <li>Drug sensitivity tests</li> <li>Drug development</li></ul> <p class=\"font-avenir font-sm text-base md:px-24 tracking-mid leading-6\">We are developing broad\n                    collaborations with various stakeholders to investigate other\n                    potential avenues of investigation and treatment.</p> <h3 class=\"font-raleway font-sm text-2xl md:mb-4 mt-10 pb-5\">3D PRINTING IN CLINICS</h3> <p class=\"font-avenir md:px-24 font-sm text-base tracking-mid leading-6\">We are interested in applying\n                    3D printing techniques in clinical contexts to create\n                    realistic and anatomically precise 3D printed models generated from medical images for surgery\n                    planning, training, and teaching applications. Together with other medical departments and hospital\n                    services, we have started a pioneer work to provide them with these complex and personalized 3D\n                    realistic models that can potentially be used in their surgeries or teaching activities. BTELab\n                    facilities include the most advanced technologies to create the models in different polymers and\n                    resins, controlling not only colors and textures but also stiffness and elongation. Our expertise in\n                    medical image processing and digital model manipulation is essential to be capable of extracting the\n                    whole complexity and anatomical structure of the human organs and tissues from the medical images.\n                </p></div>")],2),_vm._ssrNode(" "),_vm._ssrNode("<div class=\"container hidden mx-auto xl:flex\">","</div>",[_vm._ssrNode("<div class=\"w-1/3 bg-grey h-auto\">","</div>",[_vm._ssrNode("<div class=\"flex flex-col h-full justify-center items-center pt-36 pb-16\">","</div>",[_c('nuxt-img',{staticClass:"object-contain pb-8",attrs:{"format":"webp","src":_vm.manufacturing1,"alt":"Manufacturing 1","fetchpriority":"low","width":"216","height":"144","blur-up":"","quality":"100"}}),_vm._ssrNode(" "),_c('nuxt-img',{staticClass:"object-contain pb-8",staticStyle:{"object-fit":"scale-down"},attrs:{"format":"webp","src":_vm.manufacturing2,"alt":"Manufacturing 2","fetchpriority":"low","width":"176","height":"186","blur-up":"","quality":"100"}}),_vm._ssrNode(" "),_c('nuxt-img',{staticClass:"object-contain",staticStyle:{"object-fit":"scale-down"},attrs:{"format":"webp","src":_vm.manufacturing3,"alt":"Manufacturing 3","fetchpriority":"low","width":"180","height":"145","blur-up":"","quality":"100"}})],2)]),_vm._ssrNode(" <div class=\"w-2/3 bg-white md:px-20 py-32 text-center\"><h2 class=\"font-raleway font-thin lg:text-5xl text-3xl mb-16\">Additive manufacturing and 3D\n                    bioprinting</h2> <p class=\"font-avenir md:px-24 font-sm text-base tracking-mid leading-6\">3D bioprinting is an emerging\n                    technology that can spatially control the construction\n                    process of engineered tissues. The BTELab focuses on controlling the printed constructs'\n                    architecture, morphology, and biological properties to generate 3D platforms that reproduce their\n                    human equivalents fairly. Our latest works show an improvement in this technology for different\n                    aspects, such as greater functionality of the printed tissues through better vascularization and\n                    exploring the synergy with other additive manufacturing techniques. Our team's multidisciplinarity\n                    enables the control of the process from the early stage of machine design to cell preparation, which\n                    allows us to adapt the 3D printers to the specific needs of different projects and research\n                    lines.<br>The main applications of our bioprinted constructs in the lab are the following:</p> <ul class=\"font-avenir list-inside list-disc  font-sm text-base align-center my-3 tracking-mid\"><li>Study of tissue and organ regeneration</li> <li>Explore mechanisms of diseases</li> <li>Drug sensitivity tests</li> <li>Drug development</li></ul> <p class=\"font-avenir px-24 font-sm text-base tracking-mid leading-6\">We are developing broad\n                    collaborations with various stakeholders to investigate other\n                    potential avenues of investigation and treatment.</p> <h3 class=\"font-raleway font-thin text-xl mb-4 mt-10\">3D PRINTING IN CLINICS</h3> <p class=\"font-avenir px-24 font-sm text-base tracking-mid leading-6\">We are interested in applying 3D\n                    printing techniques in clinical contexts to create\n                    realistic and anatomically precise 3D printed models generated from medical images for surgery\n                    planning, training, and teaching applications. Together with other medical departments and hospital\n                    services, we have started a pioneer work to provide them with these complex and personalized 3D\n                    realistic models that can potentially be used in their surgeries or teaching activities. BTELab\n                    facilities include the most advanced technologies to create the models in different polymers